@import url(https://fonts.googleapis.com/css?family=Cabin|Kaushan+Script|Righteous&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::selection {
  color: white;
  background-color: #44E5E7;
}

html {
  font-size: 62.5%;
  font-family: Cabin, sans-serif;
}

body {
  width: 100%;
  height: 100vh;
}
.TodoForm-container {
    width: 100%;
    display: flex;
    margin-top: 1rem;
    border: 0.1rem solid #CFD2D1;
    border-radius: 15rem;
    transition: all 0.5s ease-in-out;
}

.TodoForm-container:focus-within {
    border: 0.1rem solid transparent;
    box-shadow: 0 0 0.5rem 0.2rem #44E5E7;
}

.TodoForm-input {
    padding: 2rem;
    font-size: 2.5rem;
    font-family: inherit;
    border: none;
    outline: none;
    border-radius: 15rem;
    color: #123838;
    width: 100%;
}

.TodoForm-input::-webkit-input-placeholder {
    color: #CFD2D1;
}

.TodoForm-input:-ms-input-placeholder {
    color: #CFD2D1;
}

.TodoForm-input::-ms-input-placeholder {
    color: #CFD2D1;
}

.TodoForm-input::placeholder {
    color: #CFD2D1;
}

.TodoForm-button {
    font-size: 2.5rem;
    border: none;
    outline: none;
    color: #44E5E7;
    background-color: transparent;
    cursor: pointer;
    padding: 2rem;
}

@media (max-width: 830px) {
    .TodoForm-input {
        font-size: calc(2vmin + 0.5rem);
    }
    .TodoForm-button {
        font-size: calc(2vmin + 0.5rem) !important;
    }
}
.TodoItem {
    border: 0.1rem solid #44E5E7;
    border-radius: 15rem;
    margin: 0.5rem 0;
    padding: 2rem;
    display: flex;
}

.TodoItem-text {
    font-size: 2.5rem;
    color: #123838;
    margin-right: auto;
    transition: color 0.75s;
}

.TodoItem-inactive {
    color: #44E5E7;
    position: relative;
}

.TodoItem-inactive::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 0.3rem;
  border-radius: 15rem;
  background-color: #44E5E7;
  -webkit-animation: strike 0.5s forwards;
          animation: strike 0.5s forwards;
}

@-webkit-keyframes strike {
    0% {
        width: 0%;
    } 100% {
        width: 100%;
    }
}

@keyframes strike {
    0% {
        width: 0%;
    } 100% {
        width: 100%;
    }
}

.TodoItem-button {
    font-size: 2.5rem;
    border: none;
    outline: none;
    color: #44E5E7;
    background-color: transparent;
    cursor: pointer;
}

.TodoItem-button.done {
    margin-left: 1rem;
    padding-left: 0.5rem;
}

.TodoItem-button.edit {
    margin-left: 1rem;
}

.TodoItem-button.delete {
    margin-left: 1rem;
    padding: 0 0.5rem;
}

.TodoItem-button.save {
    padding: 2rem;
}

.TodoItem-input {
    padding: 2rem;
    font-size: 2.5rem;
    font-family: inherit;
    border: none;
    outline: none;
    border-radius: 15rem;
    color: #123838;
    width: 100%;
}

.TodoItem-input::-webkit-input-placeholder {
    color: #CFD2D1;
}

.TodoItem-input:-ms-input-placeholder {
    color: #CFD2D1;
}

.TodoItem-input::-ms-input-placeholder {
    color: #CFD2D1;
}

.TodoItem-input::placeholder {
    color: #CFD2D1;
}

.TodoItem-form {
    width: 100%;
    display: flex;
    border-radius: 15rem;
    border: 0.1rem solid #44E5E7;
    margin: 0.5rem 0;
}

@media (min-width: 830px) {
    .TodoItem:hover .TodoItem-button.edit {
        visibility: visible;
        -webkit-animation: fade-in 0.5s forwards;
                animation: fade-in 0.5s forwards;
    }

    .TodoItem:hover .TodoItem-button.delete {
        visibility: visible;
        -webkit-animation: fade-in 0.5s forwards;
                animation: fade-in 0.5s forwards;
    }

    .TodoItem:not(:hover) .TodoItem-button.edit {
        -webkit-animation: fade-out 0.5s forwards;
                animation: fade-out 0.5s forwards;
    }

    .TodoItem:not(:hover) .TodoItem-button.delete {
        -webkit-animation: fade-out 0.5s forwards;
                animation: fade-out 0.5s forwards;
    }
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 0;
    } 100% {
        opacity: 1;
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
    } 100% {
        opacity: 1;
    }
}

@-webkit-keyframes fade-out {
    0% {
        opacity: 1;
    } 100% {
        opacity: 0;
        visibility: hidden;
    }
}

@keyframes fade-out {
    0% {
        opacity: 1;
    } 100% {
        opacity: 0;
        visibility: hidden;
    }
}

@media (max-width: 830px) {
    .TodoItem-text {
        font-size: calc(2vmin + 0.5rem);
    }

    .TodoItem-button {
        font-size: calc(2vmin + 0.5rem) !important;
    }
    
    .TodoItem-input {
        font-size: calc(2vmin + 0.5rem) !important;
    }
}
.TodoList {
    display: flex;
    flex-direction: column;
    width: 65%;
    margin: 0 auto;
    padding: 10rem 1rem 5rem 1rem;
}

.slide-enter {
  max-height: 0;
  opacity: 0;
  transform: translateY(5rem);
}

.slide-enter-active {
  opacity: 1;
  max-height: 10rem;
  transition: all 0.5s;
  transform: translateY(0);
}

.slide-exit {
  opacity: 1;
  max-height: 10rem;
  transform: translateY(0);
}

.slide-exit-active {
  opacity: 0;
  max-height: 0;
  transition: all 0.5s;
  transform: translateY(5rem);
}

@media (max-width: 1020px) {
    .TodoList {
        width: 80%;
    }
}

@media (max-width: 600px) {
    .TodoList {
        width: 90%;
    }
}

@media (max-width: 415px) {
    .TodoList {
        width: 100%;
    }
}

.App {
    width: 100%;
    height: 100vh;
}

.App-logo {
    position: fixed;
    z-index: 1;
    padding-top: 2rem;
    padding-bottom: 2.5rem;
    top: 0;
    left: 0;
    width: 100%;
    background-image: linear-gradient(180deg, #ffffff 65%, #ffffff00 100%);
}

.App-logo-text {
    font-size: 5rem;
    text-align: center;
}

.App-logo-text > .todo {
    font-family: "Righteous";
    color: #44E5E7;
}

.todo > .done {
    font-size: 4.3rem;
}

.App-logo-text > .list {
    font-family: "Kaushan Script";
    color: #123838;
}
